<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card class="mb-1">
        <b-row class="pb-2">
          <b-col>
            <h6>
              <b-badge variant="primary">  / </b-badge>
              <b-badge variant="danger">5</b-badge>
            </h6>
          </b-col>
        </b-row>
        <b-row class="pb-2">
          <b-col lg="3">
            <h6> </h6>
          </b-col>
          <b-col lg="6">
            <b-form-radio-group id="btnradios2"
              button-variant="outline-dark"
              v-model="selected"
              :options="options"
              name="radioBtnOutline"
            />
          </b-col>
          <b-col>
            <b-button-group>
              <b-btn :pressed.sync="btnShowFileInput" size="sm" variant="outline-dark" v-b-toggle.collapse-file-input><i class="icon-camera"></i></b-btn>
              <b-btn :pressed.sync="btnShowTextInput" size="sm" variant="outline-dark" v-b-toggle.collapse-text-input><i class="icon-pencil"></i></b-btn>
              <b-btn :pressed.sync="btnShowHelpMessages" size="sm" variant="outline-dark" v-b-toggle.collapse-card-body><i class="icon-question"></i></b-btn>
              <b-btn @click="closeAllCards" class="ml-1" size="sm" variant="outline-danger" v-show="showCloseAllCards"><i class="icon-close"></i></b-btn>
            </b-button-group>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-collapse id="collapse-text-input" v-model="collapseTextInput">
              <b-card>
                <b-form-group label="">
                  <b-form-input></b-form-input>
                </b-form-group>
              </b-card>
            </b-collapse>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-collapse id="collapse-file-input" v-model="collapseFileInput">
              <b-card>
                <b-form-group label="">
                  <b-form-file accept="image/*" :plain="true"></b-form-file>
                </b-form-group>
              </b-card>
            </b-collapse>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-collapse id="collapse-card-body" v-model="collapseHelpMessages">
              <b-card class="overflow-scroll">
                <carousel
                  :perPage="1"
                  :navigationEnabled="true"
                  :navigationClickTargetSize="4">
                  <slide>
                    <b></b>
                    <p>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Corporis asperiores, temporibus magni reiciendis blanditiis excepturi ipsum iure suscipit tempore assumenda modi consectetur voluptatem voluptatibus, officiis dolorem praesentium ipsa quisquam velit.
                    </p>
                  </slide>
                  <slide>
                    <b></b>
                    <p>
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Corporis asperiores, temporibus magni reiciendis blanditiis excepturi ipsum iure suscipit tempore assumenda modi consectetur voluptatem voluptatibus, officiis dolorem praesentium ipsa quisquam velit.
                    </p>
                  </slide>
                </carousel>
              </b-card>
            </b-collapse>
          </b-col>
        </b-row>
      </b-card>
    </div>
  </div>
</template>


<script>
import { Carousel, Slide } from 'vue-carousel'
export default {
  name: 'checklist-start',

  components: {
    Carousel,
    Slide
  },

  data () {
    return {
      btnShowFileInput: false,
      btnShowTextInput: false,
      btnShowHelpMessages: false,
      collapseFileInput: false,
      collapseTextInput: false,
      collapseHelpMessages: false,
      selected: 'radio1',
      options: [
        { text: '1', value: 'radio1' },
        { text: '2', value: 'radio2' },
        { text: '3', value: 'radio3' },
        { text: '4', value: 'radio4' },
        { text: '5', value: 'radio5' },
        { text: '6', value: 'radio6' },
        { text: '7', value: 'radio7' }
      ]
    }
  },

  methods: {
    closeAllCards () {
      this.btnShowFileInput = false
      this.btnShowTextInput = false
      this.btnShowHelpMessages = false
      this.collapseFileInput = false
      this.collapseTextInput = false
      this.collapseHelpMessages = false
    }
  },

  computed: {
    showCloseAllCards () {
      return this.btnShowFileInput || this.btnShowTextInput || this.btnShowHelpMessages
    }
  }
}
</script>

<style scoped>
  .overflow-scroll {
    position: relative;
    max-height: 250px;
    overflow-y: scroll;
  }
</style>


